.footer{
    width:100%;
    padding:1rem 0;
    background-color:rgba(19, 19, 19, 0.8)
}

.footer-container{
    max-width:1140px;
    margin:auto;
    display:grid;
    grid-template-columns:repeat(2,1fr);
    grid-gap:4px;    
}

.left{
    height:100%;
    max-width:100%;
    display:flex;
    flex-direction:column;
    margin-left:0;
    padding:1rem;
}

.right{
    height:100%;
    max-width:100%;
    display:flex;
    flex-direction:column;
    padding:1rem;
}

.social{
    margin-top: 1rem;
}

.left h4{
    font-size:1.2rem;
    padding-bottom:0.7rem;
}

h4{
    font-size:1.2rem;
}

p{
    font-size:1.2rem;   
}

@media screen and (max-width:640px){
    .footer-container{
       grid-template-columns:1fr; 
    }
}